import React, { useState, useEffect } from 'react'
//import clsx from 'clsx'

import Events from '../controller/Events'


/*
 *
 */
const ROUTES = {
	configuration: require('./ConfigurationPage').default,
	donation: require('./DonationPage').default,
	notifications: require('./Notifications/Drawer').default,
	//logs: require('./LogsPage/LogsPage').default,
}


export default function Pages(props) {
	
	let pages = [];
	Object.keys(ROUTES).forEach(page => {
		const route = ROUTES[page] || null;
		pages.push(
			<Page key={page} route={route} page={page} />
		);
	});
	
	return pages;
}

function Page(props) {
	const { route, page } = props;
	
	const onNavigate = () => setHidden(true);
	const [hidden,setHidden] = useState(true);
	
	useEffect(() => {
		const handleEvent = e => page === e && setHidden(false);
		
		const events = new Events();
		events.on('page.navigate', handleEvent);
		return () => events.off('page.navigate', handleEvent);
		
	}, [page]);
	
	
	const Route = route;
	return (
		<Route
			open={!hidden}
			navigate={onNavigate}
			/>
	);
}
